/**
 * Fonts
 * ----------------------------------------------------------------------------
 * I'm a simple guy with simple needs, I don't like to mix a lot of different
 * fonts in my designs.  To keep it simple, I have two different font types:
 * copy and headlines.  They are exactly what they sound like.
 *
 * Make sure you also define "fallbacks" on the off chance Google fonts goes
 * down or your user is on a crappy connection.
 *
 */
/**
 * Elements are global styles.  They are very generic, in fact, you
 * should think long and hard before adding anything to this stylesheet.
 * Do not add font sizes or colors in here...yes even if it's global
 * because the truth is it probably isn't as global as you think.
 */
* {
  box-sizing: inherit;
  -webkit-overflow-scrolling: touch; }
  *:before, *:after {
    box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-family: Hind, Helvetica Neue, sans-serif;
  font-size: 62.5%;
  font-weight: 400;
  height: 100%;
  letter-spacing: 0; }

body {
  background: #fff;
  color: #000;
  height: 100%;
  margin: 0;
  min-width: 320px;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important; }

img {
  height: auto;
  width: 100%; }

h1, h2, h3, h4, h5, h6 {
  font-family: Hind, Helvetica Neue, sans-serif;
  font-weight: 700;
  letter-spacing: -1px;
  margin: 0;
  padding: 0; }

b, strong {
  font-weight: 700; }

p, ul, ol {
  font-size: inherit; }

a {
  color: inherit;
  font-family: inherit;
  font-weight: inherit;
  text-decoration: none; }

p {
  margin: 0;
  padding: 0; }

nav ol, nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  nav ol li, nav ul li {
    margin: 0;
    padding: 0; }

.align--center {
  text-align: center; }

.align--left {
  text-align: left; }

.align--right {
  text-align: right; }

.person--round .person__mouth {
  margin: 10px 15px;
  width: 85%; }

.person {
  padding: 10px 0;
  width: 100%; }
  .person--round {
    border-radius: 50%;
    padding: 20px; }
  .person--round .person__mouth {
    color: #444; }
  .person__arms {
    width: 100%; }
  .person--muscular {
    width: 75%; }
  .person--muscular .person__arms {
    width: 20%; }
